import axios from "axios";
import qs from "qs";
import { Message } from "element-ui";
import router from "../router";
import store from '@/vuex/store'
import { setCookie, getCookie, deleteCookie } from "../assets/js/cookieHandle";

//POST()
axios.interceptors.request.use(
  config => {
    // 
    if (
      config.method === "post" ||
      config.method === "put" ||
      config.method === "delete"
    ) {
      // 
      config.data = qs.stringify(config.data);
    }
    
    // token , token
    if (getCookie("token")) {
      config.headers.common['X-USER-TOKEN'] = getCookie("token");
    }
    
    return config;
  },
  error => {
    Message({
      //  ,toast
      showClose: true,
      message: error.data.errmsg,
      type: "error"
    });
    return Promise.reject(error.data.errmsg);
  }
);

//()
axios.interceptors.response.use(
  res => {
    //
    if (res.data && res.data.errcode != 0) {
      if (res.data.errcode == 10006) { // 
        store.dispatch('setBaseLoginUserInfo', '');
        deleteCookie("token");
        // Message({
        //   message: res.data.errmsg,
        //   type: 'error'
        // });
        setTimeout(() => {
          router.push({
            path: "/login"
          });
        }, 3000);
      }
      else {
        Message({
          //  ,toast
          message: res.data.errmsg,
          type: "error"
        });
      }

      return Promise.reject(res.data);
    }
    return res.data;
  },
  error => {
    // ,,token,
    // localStoragesessionStorage
    // if (!getCookie("token")) {
    //   // ,
    //   router.push({
    //     path: "/login"
    //   });
    // } else {
      // ,,
      // 
      // let lifeTime =
      //   JSON.parse(window.localStorage.getItem("loginUserBaseInfo")).lifeTime *
      //   1000;
      // let nowTime = new Date().getTime(); // 
      // console.log(nowTime, lifeTime);
      // console.log(nowTime > lifeTime);
      // if (nowTime > lifeTime) {
      //   Message({
      //     showClose: true,
      //     message: ",",
      //     type: "error"
      //   });
      //   router.push({
      //     path: "/login"
      //   });
      // } else {
        // satus ,,
        if (error.response.status === 403) {
          router.push({
            path: "/error/403"
          });
        }
        if (error.response.status === 500) {
          // router.push({
          //   path: "/error/500"
          // });
        }
        if (error.response.status === 502) {
          router.push({
            path: "/error/502"
          });
        }
        if (error.response.status === 404) {
          // router.push({
          //   path: "/error/404"
          // });
        }
      // }
      
    // }
    console.log(error.response.status);


    //  response 
    let errorInfo = error.data && error.data.errcode != 0 ? error.data.errmsg : error.data;
    return Promise.reject(errorInfo);
  }
);

// axiosplugin , Vue.use(xxxx)
export default {
  install: function (Vue, Option) {
    Object.defineProperty(Vue.prototype, "$http", { value: axios });
  }
};


